import React, { forwardRef } from 'react';
import clsx from 'clsx';
import {
    DrawerContentPropTypes,
    DrawerContentDefaultProps
} from './generated/propTypes';

const DrawerContent = forwardRef((props, externalRef) => {
    const { children, className, ...customProps } = props;

    return (
        <nav
            ref={externalRef}
            className={clsx('lmnt', 'mdc-drawer__content', className)}
            {...customProps}
        >
            {children}
        </nav>
    );
});

DrawerContent.displayName = 'DrawerContent';
DrawerContent.propTypes = DrawerContentPropTypes;
DrawerContent.defaultProps = DrawerContentDefaultProps;

export default React.memo(DrawerContent);
