import clsx from 'clsx';
import React, { forwardRef } from 'react';
import {
    DrawerScrimPropTypes,
    DrawerScrimDefaultProps
} from './generated/propTypes';

const DrawerScrim = forwardRef((props, externalRef) => {
    const { className, ...customProps } = props;
    return (
        <div
            ref={externalRef}
            {...customProps}
            className={clsx('lmnt mdc-drawer-scrim', className)}
        />
    );
});

DrawerScrim.displayName = 'DrawerScrim';
DrawerScrim.propTypes = DrawerScrimPropTypes;
DrawerScrim.defaultProps = DrawerScrimDefaultProps;

export default React.memo(DrawerScrim);
