import React, { forwardRef } from 'react';
import clsx from 'clsx';
import {
    DrawerAppContentPropTypes,
    DrawerAppContentDefaultProps
} from './generated/propTypes';

const DrawerAppContent = forwardRef((props, externalRef) => {
    const { className, belowTopAppBar, children, ...customProps } = props;

    return (
        <div
            ref={externalRef}
            className={clsx(
                'lmnt',
                'mdc-drawer-app-content',
                belowTopAppBar && 'mdc-top-app-bar--fixed-adjust',
                className
            )}
            {...customProps}
        >
            {children}
        </div>
    );
});

DrawerAppContent.displayName = 'DrawerAppContent';
DrawerAppContent.propTypes = DrawerAppContentPropTypes;
DrawerAppContent.defaultProps = DrawerAppContentDefaultProps;

export default React.memo(DrawerAppContent);
