import React, { forwardRef } from 'react';
import clsx from 'clsx';
import {
    DrawerHeaderPropTypes,
    DrawerHeaderDefaultProps
} from './generated/propTypes';

const DrawerHeader = forwardRef((props, externalRef) => {
    const { children, avatar, className, title, subtitle, ...customProps } =
        props;

    let avatarRendered = null;

    if (avatar) {
        avatarRendered = React.cloneElement(avatar, {
            className: 'mdc-drawer__header-avatar'
        });
    }

    return (
        <div
            ref={externalRef}
            className={clsx('lmnt', 'mdc-drawer__header', className)}
            {...customProps}
        >
            {avatarRendered && <div>{avatarRendered}</div>}
            {title && <div className='mdc-drawer__title'>{title}</div>}
            {subtitle && <div className='mdc-drawer__subtitle'>{subtitle}</div>}
            {children}
        </div>
    );
});

DrawerHeader.displayName = 'DrawerHeader';
DrawerHeader.propTypes = DrawerHeaderPropTypes;
DrawerHeader.defaultProps = DrawerHeaderDefaultProps;

export default React.memo(DrawerHeader);
