import { MDCDrawer } from '@material/drawer';
import clsx from 'clsx';
import React, {
    useEffect,
    useMemo,
    useState,
    useRef,
    forwardRef,
    useImperativeHandle
} from 'react';
import { useEvent } from 'react-use';
import DrawerScrim from './DrawerScrim';
import { DrawerPropTypes, DrawerDefaultProps } from './generated/propTypes';

const Drawer = forwardRef((props, externalRef) => {
    const {
        belowTopAppBar,
        children,
        className,
        dismissible,
        modal,
        onClose,
        onOpen,
        open,
        variant,
        ...customProps
    } = props;
    const [ref, setRef] = useState(null); // Initialize to null so that useEvent doesn't initially listen to the global window
    const [mdcDrawer, setMdcDrawer] = useState(null);
    const [isInitiallyOpen] = useState(() => !!open);

    const drawerRef = useRef();
    useImperativeHandle(externalRef, () => drawerRef.current, []);

    const isDismissible = variant === 'dismissible' || dismissible;
    const isModal = variant === 'modal' || modal;
    const isCloseable = isDismissible || isModal;

    useEffect(() => {
        const drawer = ref && isCloseable ? MDCDrawer.attachTo(ref) : null;

        setMdcDrawer(drawer);

        return () => {
            drawer?.destroy();
        };
    }, [isCloseable, ref]);

    useEvent('MDCDrawer:opened', onOpen, ref);
    useEvent('MDCDrawer:closed', onClose, ref);

    useEffect(() => {
        if (mdcDrawer) {
            mdcDrawer.open = open;
        }
    }, [mdcDrawer, open]);

    const classes = useMemo(
        () =>
            clsx(
                'lmnt',
                'mdc-drawer',
                !variant && 'lmnt-drawer--permanent',
                isInitiallyOpen && 'mdc-drawer--open',
                isDismissible && 'mdc-drawer--dismissible',
                isModal && 'mdc-drawer--modal',
                belowTopAppBar && 'mdc-top-app-bar--fixed-adjust',
                className
            ),
        [
            belowTopAppBar,
            className,
            isDismissible,
            isInitiallyOpen,
            isModal,
            variant
        ]
    );

    return (
        <>
            <aside ref={setRef} className={classes} {...customProps}>
                {children}
            </aside>
            {isModal && <DrawerScrim />}
        </>
    );
});

Drawer.displayName = 'Drawer';
Drawer.propTypes = DrawerPropTypes;
Drawer.defaultProps = DrawerDefaultProps;
export default React.memo(Drawer);
